<template>
   
  <h1>Patients List</h1>
    
   <table class="table table-hover table-bordered"  data-page-length="10" data-order="[[ 1, &quot;asc&quot; ]]" id="example">
    <thead>
      <tr>
        <th scope="col">First Name</th>
        <th scope="col">Last Name</th>
        <th scope="col">Gender</th>
        <th scope="col">EMR No</th>
        <th scope="col">Home No</th>
        <th scope="col">Office No</th>
        <th scope="col">Zip Code</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in list" :key="item.id">
          
        <td>{{ item.first_name }}</td>
        <td>{{ item.last_name }}</td>
        <td>{{ item.gender }}</td>
        <td>{{ item.emr }}</td>
        <td>{{ item.home_phone }}</td>
        <td>{{ item.work_phone }}</td>
        <td>{{ item.zip }}</td>
      </tr>
       
    </tbody>
  </table>
   
</template>
 
<script>
import 'bootstrap/dist/css/bootstrap.min.css';
import 'jquery/dist/jquery.min.js';
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery'; 
 
 
import axios from 'axios';
export default {
  
  mounted(){
  
    axios
    .get("https://api.jsonbin.io/b/613f5cebaa02be1d44476747")
    .then((res)=>
    {
      this.list = res.data;
    
    }).then(()=>{
$('#example').DataTable();
    })
  },
  data: function() {
        return {
            list:[]
        }
    },
}
</script>